.slider {
  position: relative;
  height: 100%;
  width: 100%;
}

.slider-img-container {
  height: 100%;
  width: 100%;
  overflow: hidden;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.slider-imgs {
  height: 100%;
  width: 100%;
  position: absolute;
  -webkit-transition: top 500ms;
  -o-transition: top 500ms;
  transition: top 500ms;
}

.slider img{
  width: 100%;
  height: 100%;
  -o-object-fit: cover;
     object-fit: cover;
  display: block;
}


